import * as actionTypes from './actionTypes'
import axios from 'axios'

export const authStart = () => {
    return {
        type: actionTypes.AUTH_START
    }
}

export const authSuccess = (token,userId) => {
    return {
        type: actionTypes.AUTH_SUCCESS,
        token: token,
        userId: userId
    }
}

export const authFailed = (error) => {
    return {
        type: actionTypes.AUTH_FAILED,
        error: error
    }
}

export const logout = () => {
    localStorage.removeItem('token')
    localStorage.removeItem('userId')
    localStorage.removeItem('expirationDate')
    return {
        type: actionTypes.AUTH_LOGOUT
    }
}

export const checkAuthTimeout = (expTime) => {
    return (dispatch) => {
        setTimeout(() => {
            dispatch(logout())
        }, expTime*1000)
    }
}

export const auth = (email, password, isSignUp) => {
    return (dispatch) => {
        //
        dispatch(authStart())
        const authData = {
            email: email,
            password: password,
            returnSecureToken: true
        }
        let url = 'https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyAtDEivn1gCg_pUdGJI1yMRh8QfT27IwJY'
        if (!isSignUp) {
            url = 'https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyAtDEivn1gCg_pUdGJI1yMRh8QfT27IwJY'
        }
        axios.post(url,authData
        ).then( response => {
            const expDate = new Date(new Date().getTime() + response.data.expiresIn * 1000)
            localStorage.setItem('token', response.data.idToken)
            localStorage.setItem('userId', response.data.localId)
            localStorage.setItem('expirationDate', expDate)
            dispatch(authSuccess(response.data.idToken, response.data.localId))
            dispatch(checkAuthTimeout(response.data.expiresIn))
        }).catch( error => {
            //console.log(error.response.data.error.message)
            dispatch(authFailed(error.response.data.error))
        })
        
    } 
}

export const setAuthRedirectPath = (path) => {
    return {
        type: actionTypes.SET_AUTH_REDIRECT_PATH,
        path: path
    }
}

export const authCheckState = () => {
    return (dispatch) => {
        const token =  localStorage.getItem('token')
        if (!token) {
            dispatch(logout())
        } else {
            const expDate = new Date(localStorage.getItem('expirationDate'))
            if (expDate < new Date()) {
                dispatch(logout())
            } else {
                const userId = localStorage.getItem('userId')
                dispatch(authSuccess(token, userId))
                dispatch(checkAuthTimeout((expDate - new Date())/1000))
            }
            
        }

    }
}


